<template>
  <div class="login">
    <div class="top" style="width: 100%">
      <div class="login-main">
        <div class="login-title">
          <span>工作日报系统</span>
        </div>
        <van-cell-group>
          <van-field
            v-model="username"
            clearable
            label="用户名"
            placeholder="请输入用户名"
          ></van-field>
          <van-field
            v-model="password"
            clearable
            type="password"
            label="密码"
            placeholder="请输入密码"
          ></van-field>
          <van-checkbox v-model="rememberPassword">记住用户名和密码</van-checkbox>
          <van-button type="primary" size="large" @click="loginSubmit">登录</van-button>
        </van-cell-group>
      </div>
      <router-view/>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'login',
    data() {
      return {
        error: [],
        username: '',
        password: '',
        rememberPassword: true
      };
    },
    mounted() {
      this.getCookie();
    },
    methods: {
      // 校验密码
      validPassword: function (password) {
        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(password);
      },
      // 记住密码 设置cookie
      setCookie(c_name, c_pwd, exdays) {
        var exdate = new Date();
        exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays); // 保存的天数
        document.cookie = 'userName=' + c_name + ';path=/;expires=' + exdate.toLocaleString();
        document.cookie = 'userPwd=' + c_pwd + ';path=/;expires=' + exdate.toLocaleString();
      },
      // 记住密码 获取cookie
      getCookie: function () {
        if (document.cookie.length > 0) {
          var arr = document.cookie.split('; '); // 这里显示的格式需要切割一下
          // console.log(arr)
          for (var i = 0; i < arr.length; i++) {
            var arr2 = arr[i].split('='); // 再次切割
            // 判断查找相对应的值
            // console.log(arr2);
            if (arr2[0] === 'userName') {
              this.username = arr2[1]; // 保存到保存数据的地方
            } else if (arr2[0] === 'userPwd') {
              this.password = arr2[1];
            }
          }
        }
      },
      // 处理登录按钮
      loginSubmit() {
        if (this.username === '') {
          this.$toast('用户名不能为空');
          return false;
        }
        if (this.password === '') {
          this.$toast('密码不能为空');
          return false;
        }
        if (this.rememberPassword === true) {
          // 传入账号名，密码，和保存天数3个参数
          this.setCookie(this.username, this.password, 7);
        } else {
          this.$api.clearCookie('userName'); // 清空Cookie
          this.$api.clearCookie('userPwd');
        }
        var md5Pswd = this.$md5(this.password).toLocaleUpperCase(); // 将密码进行MD5加密
        var obj = {
          username: this.username,
          password: md5Pswd
        };
        this.$api.post('/iims/staff/login', obj).then(res => {
          if (res.status === 1) {
            var exdate = new Date();
            exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * 7); // 保存的天数
            document.cookie = 'token' + '=' + res.data[0].token + ';path=/;expires=' + exdate.toLocaleString();
            localStorage.setItem('userInfo', JSON.stringify(res)); // 将请求到的登录用户信息数据存在Local Storage中
            this.$router.push({
              path: '/work_daily'
            });
            this.$myToast('登录成功', 'success', '1500');
          } else if (res.status === 0) {
            this.$myToast('用户名不存在', 'error', '1500');
          } else if (res.status === 2) {
            this.$myToast('密码错误，请重试！', 'error', '1500');
          }
        })
          .catch(err => {
            console.log(err);
          });

      }
    },
    watch: {
      rememberPassword(data) {
        // 判断复选框是否被勾选 勾选则调用配置cookie方法
        if (data === true) {
          // 传入账号名，密码，和保存天数3个参数
          this.setCookie(this.username, this.password, 7);
        } else {
          this.$api.clearCookie('userName'); // 清空Cookie
          this.$api.clearCookie('userPwd');
        }
      }
    }
  };
</script>

<style lang="less" scoped>
  .login{
    background-image: url("../assets/images/login/login-bg.jpeg");
    background-repeat: no-repeat;
    background-size: cover;
    width: 100vw;
    height: 100vh;
  }
  .top {
    .login-main {
      top: 4rem;
      padding: 6%;
      position: absolute;
      width: 74%;
      margin-left: 7%;
      background-color: white;
      border-radius: 8px;
      box-shadow: -1px 2px 30px 8px rgba(0, 0, 0, 0.16862745098039217);
    }

    .login-title {
      text-align: center;

      span {
        font-weight: bold;
      }
    }

    .van-hairline--top-bottom::after {
      border-width: 0;
    }

    .van-cell-group {
      margin-top: 8%;
    }

    .van-checkbox__icon--round .van-icon {
      border-radius: 0;
    }

    .van-checkbox {
      padding: 10px 15px;
      font-size: 0.6rem;

      .van-checkbox__label {
        color: #1989fa !important;
        font-size: 14px !important;
      }
    }

    .van-button--large {
      border-radius: 25px;
      background-color: #fe8f01;
      border: 1px solid #fe8f01;
      margin-top: 8%;
      height: 40px;
      line-height: 0;
    }
  }
</style>
